import React, { useState, useEffect } from "react";

import "./index.css";

import Counter from "../../components/Counter";
import CountUp from "react-countup";
import mainLogo from "../../Nets_Logo.png";
import {
  Charts,
  ChartContainer,
  ChartRow,
  YAxis,
  BarChart,
  Resizable,
  styler,
} from "react-timeseries-charts";
import { TimeSeries, Index } from "pondjs";
import _ from "lodash";
import { hydrate } from "react-dom";
import LoadingSpinner from "../../components/LoadingSpinner";

const data = [
  ["2017-01-24T00:00", 0.01, 0.5],
  ["2017-01-24T01:00", 0.13, 0.5],
  ["2017-01-24T02:00", 0.07, 0.5],
  ["2017-01-24T03:00", 0.04, 0.5],
  ["2017-01-24T04:00", 0.33, 0.5],
  ["2017-01-24T05:00", 0, 0.5],
  ["2017-01-24T06:00", 0, 0.5],
  ["2017-01-24T07:00", 0, 0.5],
  ["2017-01-24T08:00", 0.95, 0.5],
  ["2017-01-24T09:00", 1.12, 0.5],
  ["2017-01-24T10:00", 0.66, 0.5],
  ["2017-01-24T11:00", 0.06, 0.5],
  ["2017-01-24T12:00", 0.3, 0.5],
  ["2017-01-24T13:00", 0.05, 0.5],
  ["2017-01-24T14:00", 0.5, 0.5],
  ["2017-01-24T15:00", 0.24, 0.5],
  ["2017-01-24T16:00", 0.02, 0.5],
  ["2017-01-24T17:00", 0.98, 0.5],
  ["2017-01-24T18:00", 0.46, 0.5],
  ["2017-01-24T19:00", 0.8, 0.5],
  ["2017-01-24T20:00", 0.39, 0.5],
  ["2017-01-24T21:00", 0.4, 0.5],
  ["2017-01-24T22:00", 0.39, 0.5],
  ["2017-01-25T23:00", 0.28, 0.5],
];

const series = new TimeSeries({
  name: "hilo_rainfall",
  columns: ["index", "phys", "ecomm"],
  points: data.map(([d, value1, value2]) => [
    Index.getIndexString("1h", new Date(d)),
    value1,
    value2,
  ]),
});

export default function HomePage() {
  const [history, setHistory] = useState(series);
  const [startValue1, setStartValue1] = useState(null);
  const [startValue2, setStartValue2] = useState(null);
  const [startValue3, setStartValue3] = useState(null);
  const [endValue1, setEndValue1] = useState(null);
  const [endValue2, setEndValue2] = useState(null);
  const [endValue3, setEndValue3] = useState(null);
  const [isVideoLoaded, setIsVideoLoaded] = useState(false);

  const onLoadedData = () => {
    setIsVideoLoaded(true);
  };

  useEffect(() => {
    var myHeaders = new Headers();

    var requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };

    fetch(
      "https://hexatownnets.blob.core.windows.net/feeds/monitor.json",
      requestOptions
    )
      .then((response) => response.json())
      .then((result) => {
        var today = [];

        result.map((item) => {
          if (item.day === 25) {
            today.push([
              "2021-11-25T" +
                (item.hour < 10 ? "0" + item.hour : item.hour) +
                ":" +
                (item.minute < 10 ? "0" + item.minute : item.minute),
              item.online.counter,
              item.terminals.counter,
            ]);
          }
        });
        //setHistory(today)

        const series = new TimeSeries({
          name: "transactionss",
          columns: ["index", "phys", "ecomm"],
          points: today.map(([d, value1, value2]) => [
            Index.getIndexString("1h", new Date(d)),
            value1,
            value2,
          ]),
        });
        setHistory(series);
        console.log(result);
      })
      .catch((error) => console.log("error", error));

    fetch("https://nocc-api.azurewebsites.net//data", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setStartValue1(result.PhysTermStart);
        setStartValue2(result.OnlEcomStart);
        setStartValue3(result.IaltStart);
        setEndValue1(result.PhysTermSlut);
        setEndValue2(result.OnlEcomSlut);
        setEndValue3(result.IaltSlut);

        console.log(result);
      })
      .catch((error) => console.log("error", error));
  }, [startValue1]);

  const style = styler([
    { key: "phys", color: "#A5C8E1", selected: "#2CB1CF" },
    { key: "ecomm", color: "#FFCC9E", selected: "#2CB1CF" },
  ]);
  /*
  return (
    <div>
        <div className="row">
            <div className="col-md-12">
                <b>BarChart</b>
            </div>
        </div>
        <hr />
        <div className="row">
            <div className="col-md-12">
              

{!false && 
                <Resizable>
                  
                    <ChartContainer 
                      timeRange={history.range()}
                      enablePanZoom={true} >
                        <ChartRow height="150">
                            <YAxis
                                id="transactions"
                                label="Transactions"
                                min={0}
                                max={400}
                                format=".2f"
                                width="70"
                                type="linear"
                            />
                            <Charts>
                                <BarChart
                                    axis="transactions"
                                    spacing={1}
                                    columns={["phys", "ecomm"]}
                                    series={history}
                                    style={style}
                                />
                            </Charts>
                        </ChartRow>
                    </ChartContainer>
                </Resizable>}
            </div>
        </div>
    </div>
)

  return (
    <div>

      {history &&
        <div>
          <ChartContainer timeRange={history} width={800}>
            <ChartRow height="200">
              <YAxis id="axis1" label="AUD" min={0.5} max={1.5} width="60" type="linear" format="$,.2f" />
              <Charts>
                <LineChart axis="axis1" series={history} />
                
              </Charts>
              <YAxis id="axis2" label="Euro" min={0.5} max={1.5} width="80" type="linear" format="$,.2f" />
            </ChartRow>
          </ChartContainer>


        </div>
      }

    </div>

  )*/
  return (
    <div className="App">
      <video
        autoPlay="autoplay"
        muted
        playsInline
        loop="loop"
        preload="auto"
        id="myVideo"
        className="myVideo"
        onLoadedData={onLoadedData}
        style={{ opacity: isVideoLoaded ? 1 : 1 }}
      >
        <source src="video01.mp4" type="video/mp4" />
      </video>
      {!isVideoLoaded && <LoadingSpinner />}

      <div className="content">
        <div className="heading">
          <div>Realtime Transactions</div>
        </div>

        <div className="numbers">
          <div className="column">
            <div className="caption">Point of Sales</div>
            <div className="value">
              <CountUp
                start={startValue1}
                end={endValue1}
                duration={300}
                separator={"."}
                preserveValue={true}
              />
            </div>
          </div>
          <div className="column">
            <div className="caption">Online</div>
            <div className="value">
              <CountUp
                start={startValue2}
                end={endValue2}
                duration={300}
                separator={"."}
              />
            </div>
          </div>
          <div className="column">
            <div className="caption">Total</div>
            <div className="value">
              <CountUp
                start={parseInt(startValue1) + parseInt(startValue2)}
                end={parseInt(endValue1) + parseInt(endValue2)}
                duration={300}
                separator={"."}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="logoImageHolder">
        <div className="logoImage">
          <img src={mainLogo} alt="fireSpot" type="image/png" />
        </div>
      </div>
    </div>
  );
}
